.modal-background-wrapper {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}

.modal-opaque-background {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  background-color: black;
  z-index: 2;
}

.modal-box {
  position: relative;
  left: 50%;
  top: 50%;
  padding: 15px;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 50%;
  background-color: white;
  z-index: 3;
  border-radius: 5px;
}

.modal-text-area {
  border: none;
  position: relative;
  resize: none;
  min-height: 100px;
  padding: 5px;
  -webkit-box-shadow: 0 0 0 2px rgb(255, 163, 163);
          box-shadow: 0 0 0 2px rgb(255, 163, 163);
}



