html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  --main-color: rgb(82, 181, 238);
  --secondary-color: rgb(238, 95, 76);
  --tertiary-color: silver;
}

.directions-text-area,
.ingredients-text-area {
  border: none;
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  resize: none;
  min-height: 100px;
  padding: 5px;
  -webkit-box-shadow: 0 0 0 2px rgb(255, 163, 163);
          box-shadow: 0 0 0 2px rgb(255, 163, 163);
  -moz-appearance: textfield-multiline;
  -webkit-appearance: textarea;
}

/*========================================================= */
/*                           BUTTONS                        */
/*========================================================= */

/*    ----------------- Button Shared -----------------     */
.button-main,
.button-secondary,
.button-tertiary {
  padding: 20px;
  margin: 10px;
  display: inline-block;
  border-radius: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 2px solid white;
}

.button-main:hover,
.button-secondary:hover,
.button-tertiary:hover {
  cursor: pointer;
  border: 2px solid navy;
}

.button-main:active .button-secondary:active,
.button-tertiary:active {
  opacity: 0.6;
}

/* ----------------- Button Colors -----------------  */

.button-main {
  background-color: var(--main-color);
}

.button-secondary {
  background-color: var(--secondary-color);
}

.button-tertiary {
  background-color: var(--tertiary-color);
}

/*========================================================= */
/*                       END BUTTONS                        */
/*========================================================= */



